'use client';

import { useState } from 'react';
import {
  FaCheck,
  FaSpinner,
} from 'react-icons/fa';
import { useWallet } from '@/contexts/WalletContext';

// ÂÆö‰πâÁ§æ‰∫§Âπ≥Âè∞Á±ªÂûã
type SocialPlatform = 'x' | 'telegram' | 'discord';

// Á§æ‰∫§Âπ≥Âè∞ËÆ§ËØÅÁä∂ÊÄÅ
interface AuthStatus {
  x: 'pending' | 'completed' | 'not_started';
  telegram: 'pending' | 'completed' | 'not_started';
  discord: 'pending' | 'completed' | 'not_started';
}

export default function SocialAuthSection() {
  const { wallet, updateBalances } = useWallet();

  // ËÆ§ËØÅÁä∂ÊÄÅ
  const [authStatus, setAuthStatus] = useState<AuthStatus>({
    x: 'not_started',
    telegram: 'not_started',
    discord: 'not_started',
  });

  // ËÆ§ËØÅÂπ≥Âè∞ÈÖçÁΩÆ
  const platforms = [
    {
      id: 'x' as SocialPlatform,
      name: 'X (Twitter)',
      icon: "üê¶",
      reward: 200,
      description: 'ËøûÊé•Âπ∂ËÆ§ËØÅÊÇ®ÁöÑX (Twitter)Ë¥¶Âè∑ÔºåËé∑Âæó200Â∞èÁ∫¢Ëä±Â•ñÂä±',
    },
    {
      id: 'telegram' as SocialPlatform,
      name: 'Telegram',
      icon: "üì±",
      reward: 150,
      description: 'ËøûÊé•Âπ∂ËÆ§ËØÅÊÇ®ÁöÑTelegramË¥¶Âè∑ÔºåËé∑Âæó150Â∞èÁ∫¢Ëä±Â•ñÂä±',
    },
    {
      id: 'discord' as SocialPlatform,
      name: 'Discord',
      icon: "üí¨",
      reward: 180,
      description: 'ËøûÊé•Âπ∂ËÆ§ËØÅÊÇ®ÁöÑDiscordË¥¶Âè∑ÔºåËé∑Âæó180Â∞èÁ∫¢Ëä±Â•ñÂä±',
    },
  ];

  // ÂºÄÂßãËÆ§ËØÅÊµÅÁ®ã
  const startAuth = async (platform: SocialPlatform) => {
    if (!wallet.isConnected) {
      alert('ËØ∑ÂÖàËøûÊé•Èí±ÂåÖ');
      return;
    }

    // Êõ¥Êñ∞Áä∂ÊÄÅ‰∏∫pending
    setAuthStatus((prev) => ({
      ...prev,
      [platform]: 'pending',
    }));

    try {
      // Âú®ÂÆûÈôÖÂ∫îÁî®‰∏≠ÔºåËøôÈáåÂ∫îËØ•ÈáçÂÆöÂêëÂà∞Âπ≥Âè∞ËÆ§ËØÅÈ°µÈù¢ÊàñÊâìÂºÄËÆ§ËØÅÂºπÁ™ó
      // ‰∏∫ÊºîÁ§∫ÔºåÊàë‰ª¨‰ΩøÁî®setTimeoutÊ®°ÊãüÂºÇÊ≠•ËÆ§ËØÅÊµÅÁ®ã
      await new Promise((resolve) => setTimeout(resolve, 2000));

      // Ê®°ÊãüËÆ§ËØÅÊàêÂäü
      setAuthStatus((prev) => ({
        ...prev,
        [platform]: 'completed',
      }));

      // Ëé∑ÂèñÂπ≥Âè∞Â•ñÂä±Êï∞Èáè
      const reward = platforms.find((p) => p.id === platform)?.reward || 0;

      // Êõ¥Êñ∞Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù
      updateBalances({
        redFlower: (wallet.balance.redFlower || 0) + reward,
      });

      // ÊòæÁ§∫ÊàêÂäüÊ∂àÊÅØ
      alert(`ÊÅ≠ÂñúÔºÅÊàêÂäüËÆ§ËØÅ${platform}Âπ≥Âè∞ÔºåËé∑Âæó${reward}Â∞èÁ∫¢Ëä±Â•ñÂä±ÔºÅ`);
    } catch (error) {
      console.error('ËÆ§ËØÅÂ§±Ë¥•:', error);
      setAuthStatus((prev) => ({
        ...prev,
        [platform]: 'not_started',
      }));
      alert('ËÆ§ËØÅÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï');
    }
  };

  // Ê†πÊçÆËÆ§ËØÅÁä∂ÊÄÅËøîÂõûÊåâÈíÆÊñáÊú¨ÂíåÊ†∑Âºè
  const getAuthButton = (platform: SocialPlatform) => {
    const status = authStatus[platform];

    if (status === 'completed') {
      return (
        <button
          className="flex items-center justify-center gap-2 py-2 px-4 bg-green-600/30 text-green-400 rounded-md cursor-default border border-green-600/50"
          disabled
        >
          <FaCheck className="w-4 h-4" />
          Â∑≤ËÆ§ËØÅ
        </button>
      );
    }

    if (status === 'pending') {
      return (
        <button
          className="flex items-center justify-center gap-2 py-2 px-4 bg-gray-700 text-gray-300 rounded-md cursor-wait"
          disabled
        >
          <FaSpinner className="w-4 h-4 animate-spin" />
          ËÆ§ËØÅ‰∏≠...
        </button>
      );
    }

    return (
      <button
        onClick={() => startAuth(platform)}
        className="neon-button py-2 px-4 rounded-md"
      >
        ÂéªËÆ§ËØÅ
      </button>
    );
  };

  return (
    <div className="glass-card p-6">
      <h2 className="text-2xl font-bold mb-6">Á§æ‰∫§Âπ≥Âè∞ËÆ§ËØÅ</h2>
      <p className="text-gray-400 mb-6">
        ËÆ§ËØÅÊÇ®ÁöÑÁ§æ‰∫§Â™í‰ΩìË¥¶Âè∑ÔºåËé∑ÂèñÂ∞èÁ∫¢Ëä±Â•ñÂä±„ÄÇÊØè‰∏™Âπ≥Âè∞‰ªÖËÉΩËÆ§ËØÅ‰∏ÄÊ¨°„ÄÇ
      </p>

      <div className="space-y-6">
        {platforms.map((platform) => (
          <div
            key={platform.id}
            className="flex flex-col sm:flex-row items-start sm:items-center justify-between p-4 bg-black/30 rounded-lg border border-gray-800 gap-4"
          >
            <div className="flex items-center gap-4">
              <div className="p-3 rounded-full bg-black/50 border border-gray-700">
                {platform.icon}
              </div>
              <div>
                <h3 className="font-semibold text-lg">{platform.name}</h3>
                <p className="text-gray-400 text-sm">{platform.description}</p>
              </div>
            </div>

            <div className="flex items-center gap-4 ml-0 sm:ml-4">
              <div className="text-primary font-semibold">
                +{platform.reward}{' '}
                <span className="text-sm text-gray-400">Â∞èÁ∫¢Ëä±</span>
              </div>
              {getAuthButton(platform.id)}
            </div>
          </div>
        ))}
      </div>

      {!wallet.isConnected && (
        <div className="mt-6 p-4 bg-yellow-500/10 border border-yellow-500/30 rounded-md text-yellow-400">
          <p>ËØ∑ÂÖàËøûÊé•Èí±ÂåÖ‰ª•ËøõË°åÁ§æ‰∫§Âπ≥Âè∞ËÆ§ËØÅ</p>
        </div>
      )}
    </div>
  );
}
